import './styles/main.css';
import { Swiper, SwiperSlide } from 'swiper/react';
import {Pagination, EffectFade, Autoplay, Navigation} from 'swiper/modules'
import 'swiper/css';
import 'swiper/css/autoplay'
import 'swiper/css/effect-fade'
import 'swiper/css/pagination'
import { useRef } from 'react';
import { useState } from 'react';
import {ReactComponent as Map} from './images/map-main.svg'
import {ReactComponent as ArrowL} from './images/arrow-l.svg'
import {ReactComponent as ArrowR} from './images/arrow-r.svg'

export default function Main() {
    const [_, setInit] = useState();
    const prevRef = useRef(null);
    const nextRef = useRef(null);
    return(
        <main>
            <div className="background-swiper">
                <Swiper
                modules={[EffectFade, Pagination, Autoplay, Navigation]}
                navigation={{
                    prevEl: prevRef.current,
                    nextEl: nextRef.current,
                  }}
                effect='fade'
                pagination={{
                    clickable: true,
                    renderBullet: function (index, className) {
                        let labels = ['West Shore', 'North Shore', 'Sourth Shore', 'East Shore'];
                        return '<div class="' + className + '">' + (labels[index]) + '</div>'; 
                      },
                }}
                fadeEffect={{crossFade: true}}
                loop={true}
                autoplay= {{delay: 5000, stopOnLastSlide: false, disableOnInteraction: false}}
                slidesPerView={1}
                onInit={() => setInit(true)}
                >
                    <SwiperSlide>
                        <img src={require('./images/Surf_The_Wave.jpg')} alt="" />
                        <h2 className='h2'>West Shore</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide2.jpg')} alt="" />
                        <h2 className='h2'>North Shore</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide3.jpg')} alt="" />
                        <h2 className='h2'>South Shore</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide4.jpg')} alt="slide" />
                        <h2 className='h2'>East Shore</h2>
                    </SwiperSlide>
                    
                </Swiper>
            </div>
            <div className="content">
                <div className="big-text">
                    GO SURF
                </div>
                <div className="map"><Map /></div>
                <div className="text">
                    <div className="surf">
                        <div className="line"></div>
                        <p>SURF</p>               
                    </div>
                    <div className="surf">
                        <div className="line"></div>
                        <p>CONDITION</p>               
                    </div>
                    <h5>RADICAL</h5>
                </div>
                <div className="btns">
                        <button className='navigation' ref={prevRef}><ArrowL /></button>
                        <button className='navigation' ref={nextRef}><ArrowR /></button>
                </div>
            </div>
        </main>
    )
}