import './styles/header.css';
import { ReactComponent as Surf } from './images/Surf.svg';
import { ReactComponent as Travel } from './images/Travel.svg';
import { ReactComponent as Sleep } from './images/Sleep.svg';
import { ReactComponent as Shop } from './images/Shop.svg';

export default function Header() {
    const day = new Date().getUTCDate()
    const mouth = new Date().getUTCMonth() + 1
    const year = new Date().getFullYear()
    return(
        <header>
            <div className="blue"> 
            </div>
            <h1>GO - SURF</h1>
            <nav>
                <a href='#surf' className="nav-el">
                    <Surf />
                    <p>Surf</p>
                </a>
                <a href='#travel' className="nav-el">
                    <Travel />
                    <p>Travel</p>
                </a>
                <a href='#sleep' className="nav-el">
                    <Sleep />
                    <p>Sleep</p>
                </a>
                <a href='#shop' className="nav-el">
                    <Shop />
                    <p>Shop</p>
                </a>
            </nav>
            <div className="date">
                <div className="day">{('0'+ day).slice(-2)}</div>
                <div className="line"></div>
                <div className="mouth-year">
                    <div className="mouth">{('0'+ mouth).slice(-2)}</div>
                    |
                    <div className="year">{year}</div>
                </div>
            </div>
        </header>
    )
}