import './styles/shop.css'
import { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import {EffectFade, Autoplay, Navigation} from 'swiper/modules'
import 'swiper/css';
import 'swiper/css/autoplay'
import 'swiper/css/effect-fade'
import 'swiper/css/pagination'
import { ReactComponent as Arrow } from './images/arrow.svg';
import { ReactComponent as Stars } from './images/stars.svg';

export default function Shop() {
    const [guests, setGuests] = useState(5);
    const [nights, setNights] = useState(4);
    const [_, setInit] = useState();
    const prevRef = useRef(null);
    const nextRef = useRef(null);
    return(
        <article id='shop' className="shop">
            <div className="bg">
                <img src={require('./images/bg-shop.png')} />
            </div>
            <div className="title">
                <div className="line"></div>
                <p>SHOP</p>
                <h2>SHOP</h2>
            </div>
            <div className="btns">
                    <button className='prev' ref={prevRef}><Arrow /></button>
                    <button className='next' ref={nextRef}><Arrow /></button>
            </div>
            <div className="left">
            <div id='title-min' className="title-min">
                <div className="over">
                    <div className="line-min"></div>
                    <p>Style</p>
                </div> 
                <div id='rating' className="rating">
                    <Stars />
                </div>
                  

                <a className="go">
                    <p>DROP</p>
                    <div className="blue">
                        <div className="mini-line"></div>
                        <p>IN <Arrow /></p>
                    </div> 
                </a>            
            </div>
            </div>
            <Swiper
                modules={[EffectFade, Autoplay, Navigation]}
                navigation={{
                    prevEl: prevRef.current,
                    nextEl: nextRef.current,
                  }}
                effect='fade'
                fadeEffect={{crossFade: true}}
                loop={true}
                autoplay= {{delay: 5000, stopOnLastSlide: false, disableOnInteraction: false}}
                slidesPerView={1}
                onInit={() => setInit(true)}
                >
                <SwiperSlide>
                        <img className='slide' src={require('./images/slide1.png')} alt="" />
                        <h2>North Nugget TT Surfboard</h2>
                        <h3>$1089 <span>99</span></h3>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img className='slide' src={require('./images/slide2.png')} alt="" />
                        <h2>Surftech Roller 0,8 Surfboard</h2>
                        <h3>$799 <span>99</span></h3>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img className='slide' src={require('./images/slide3.png')} alt="" />
                        <h2>Motto BFF 6,0 RNF Surfboard</h2>
                        <h3>$569 <span>99</span></h3>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img className='slide' src={require('./images/slide4.png')} alt="slide" />
                        <h2> Slatter Designs 9,0 Surfboard</h2>
                        <h3>$829 <span>99</span></h3>
                    </SwiperSlide>  
                    <div className="info-surf">
                    <div className="info info1">
                        <div className="point">+</div>
                        <div className="hidden">
                            <div className="line"></div>
                            <p>Double Concave with Vee Shape low point</p>
                        </div>
                    </div>
                    <div className="info info2">
                        <div className="point">+</div>
                        <div className="hidden">
                            <div className="line"></div>
                            <p>Smooth edges for the safety of your limbs</p>
                        </div>
                    </div>
                    <div className="info info3">
                        <div className="point">+</div>
                        <div className="hidden">
                            <div className="line"></div>
                            <p>Special fins for better dinimics</p>
                        </div>
                    </div>
                </div>
                    <div id='extras' className="extras">
                        <h2>Extras</h2>
                        <div className="cont">
                            <div className="extrases">
                                <div><img src={require('./images/Wax.png')} alt="" /></div>
                                <p className="name">SEX WAX</p>
                                <p className="sale">$24.99</p>
                            </div>
                            <div className="extrases">
                                <div><img src={require('./images/Pur.png')} alt="" /></div>
                                <p className="name">PURA VIDA</p>
                                <p className="sale">$27.99</p>
                            </div>
                        </div>
                </div>
                </Swiper>
                
                
            

                
        </article>
    )
}