import './styles/travel.css'
import { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import {EffectFade, Autoplay, Navigation} from 'swiper/modules'
import 'swiper/css';
import 'swiper/css/autoplay'
import 'swiper/css/effect-fade'
import 'swiper/css/pagination'
import { ReactComponent as Arrow } from './images/arrow.svg';

export default function Travel() {
    const [_, setInit] = useState();
    const prevRef = useRef(null);
    const nextRef = useRef(null);
    return(
        <article id='travel' className="travel">
            <div className="title">
                <div className="line"></div>
                <p>TRAVEL</p>
                <h2>TRAVEL</h2>
            </div>

            <div id='title-min' className="title-min">
                <div className="over">
                    <div className="line-min"></div>
                    <p>Shore</p>
                </div>
                <div className="btns">
                    <button className='prev' ref={prevRef}><Arrow /></button>
                    <button className='next' ref={nextRef}><Arrow /></button>
                </div>
                
            </div>
            <Swiper
                modules={[EffectFade, Autoplay, Navigation]}
                navigation={{
                    prevEl: prevRef.current,
                    nextEl: nextRef.current,
                  }}
                effect='fade'
                fadeEffect={{crossFade: true}}
                loop={true}
                autoplay= {{delay: 5000, stopOnLastSlide: false, disableOnInteraction: false}}
                slidesPerView={1}
                onInit={() => setInit(true)}
                >
                <SwiperSlide>
                        <img src={require('./images/slide1.png')} alt="" />
                        <h2>AIRLIE BEACH <span>|</span> AUSTRALIA</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide2.png')} alt="" />
                        <h2>TOKAS BEACH <span>|</span> JAPAN</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide3.png')} alt="" />
                        <h2>HOTO BEACH <span>|</span> SOUTH AFRICA</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide4.png')} alt="slide" />
                        <h2>FORNIA BEACH <span>|</span> CALIFORNIA</h2>
                    </SwiperSlide>  
                </Swiper>

                <div className="bottom-slide">
                    <div className="left">
                    <div className="heading">  
                        <p>AIRLINE</p>
                        <div className="line"></div>
                    </div>
                    <h5>EXPERT AIRLINE</h5>
                    </div>
                    <div className="img">
                        <img src={require('./images/plane.png')} alt="" />
                    </div>
                </div>
                <div className="info">
                    <div className="block">
                        <h6>Destination</h6>
                        <p className="var">Queensland Australia</p>
                    </div>

                    <div className="block">
                        <h6>Distance</h6>
                        <p className="var">7,065 Miles</p>
                    </div>

                    <div className="block">
                        <h6>Travel Time</h6>
                        <p className="var">23 Hours 5 Minutes</p>
                    </div>

                    <div className="block">
                        <h6>Pricing</h6>
                        <p className="var">$1,976 USD</p>
                    </div>
                </div>
            
                <a className="go">
                    <p>VIEW</p>
                    <div className="blue">
                        <div className="mini-line"></div>
                        <p>SURF <Arrow /></p>
                    </div> 
                    <img className='cocktail' src={require('./images/cocktail.png')} alt="" />
                </a>
                
        </article>
    )
}