import './styles/sleep.css'
import { useRef, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import {EffectFade, Autoplay, Navigation} from 'swiper/modules'
import 'swiper/css';
import 'swiper/css/autoplay'
import 'swiper/css/effect-fade'
import 'swiper/css/pagination'
import { ReactComponent as Arrow } from './images/arrow.svg';
import { ReactComponent as Stars } from './images/stars.svg';

export default function Sleep() {
    const [guests, setGuests] = useState(5);
    const [nights, setNights] = useState(4);
    const [_, setInit] = useState();
    const prevRef = useRef(null);
    const nextRef = useRef(null);
    return(
        <article id='sleep' className="sleep">
            <div className="title">
                <div className="line"></div>
                <p>SLEEP</p>
                <h2>SLEEP</h2>
            </div>

            <div id='title-min' className="title-min">
                <div className="over">
                    <div className="line-min"></div>
                    <p>Resorts</p>
                </div>
                <div className="btns">
                    <button className='prev' ref={prevRef}><Arrow /></button>
                    <button className='next' ref={nextRef}><Arrow /></button>
                </div>
                
            </div>
            <Swiper
                modules={[EffectFade, Autoplay, Navigation]}
                navigation={{
                    prevEl: prevRef.current,
                    nextEl: nextRef.current,
                  }}
                effect='fade'
                fadeEffect={{crossFade: true}}
                loop={true}
                autoplay= {{delay: 5000, stopOnLastSlide: false, disableOnInteraction: false}}
                slidesPerView={1}
                onInit={() => setInit(true)}
                >
                <SwiperSlide>
                        <img src={require('./images/slide1.png')} alt="" />
                        <h2>AUBERGE <span>|</span> AUSTRALIA</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide2.png')} alt="" />
                        <h2>KOUSHIRI <span>|</span> JAPAN</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide3.png')} alt="" />
                        <h2>FETORIN <span>|</span> SOUTH AFRICA</h2>
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={require('./images/slide4.png')} alt="slide" />
                        <h2>BERCONIO <span>|</span> CALIFORNIA</h2>
                    </SwiperSlide>  
                </Swiper>

                <div className="bottom-slide">
                    <div className="left">
                    <div className="heading">  
                        <p>RATING</p>
                        <div className="line"></div>
                    </div>
                    <h5>EXCELLENT <Stars /></h5>
                    </div>
                    <div className="img">
                        <img src={require('./images/hammock.png')} alt="" />
                    </div>
                </div>
                <div className="info">
                    <div className="block">
                        <h6>Resort</h6>
                        <p className="var">Auberge Australia</p>
                    </div>

                    <div className="block">
                        <h6># of Nights</h6>
                        <div>
                            <p className="var">{nights} nights</p>
                            <div className="buttons">
                                <button onClick={() => setNights(prev => prev !== 1 ? prev-1 : prev)}>-</button>
                                <button onClick={() => setNights(prev => prev !== 30 ? prev+1 : prev)}>+</button>
                            </div>
                        </div>
                    </div>

                    <div className="block">
                        <h6># of Guests</h6>
                        <div>
                            <p className="var">{guests} Guests</p>
                            <div className="buttons">
                                <button onClick={() => setGuests(prev => prev !== 1 ? prev-1 : prev)}>-</button>
                                <button onClick={() => setGuests(prev => prev !== 7 ? prev+1 : prev)}>+</button>
                            </div>
                        </div>
                    </div>

                    <div className="block">
                        <h6>Pricing</h6>
                        <p className="var">$349 USD</p>
                        
                    </div>
                </div>
            
                <a className="go">
                    <p>BOOK</p>
                    <div className="blue">
                        <div className="mini-line"></div>
                        <p>STAY <Arrow /></p>
                    </div> 
                    <img className='cocktail' src={require('./images/cvetok.png')} alt="" />
                </a>
                
        </article>
    )
}