import './styles/surf.css'
import { Swiper, SwiperSlide } from 'swiper/react';
import {Navigation, A11y} from 'swiper/modules'
import 'swiper/css';
import {ReactComponent as Map} from './images/map.svg';
import {ReactComponent as Poi} from './images/poi.svg';
import {ReactComponent as Wave} from './images/wave.svg';
import {ReactComponent as Wind} from './images/wind.svg';
import {ReactComponent as Water} from './images/water.svg';
import {ReactComponent as Arrow} from './images/arrow.svg';
import { useRef, useState } from 'react';
// тут слайдер не нужен
export default function Surf() {
    const [_, setInit] = useState();
    const prevRef = useRef(null);
    const nextRef = useRef(null);
    return(
        <article id='surf' className="surf">
            <div className="title">
                <div className="line"></div>
                <p>SURF</p>
                <h2>SURF</h2>
            </div>

            <div className="title-min">
                <div className="over">
                    <div className="line-min"></div>
                    <p>Current location</p>
                </div>
                <p className='location'>California <span>|</span> USA</p>
            </div>
            <div className="toi">
                <Poi />
            </div>
            <div className="map">
                    <div className="main-map"><Map /></div>

                    <div className=" suff_currort suff_currort1">
                        <div className="hidden hidden1">
                            <h5 className="name">WESTERNMOST BEACH</h5>
                            <div className="line-suff"></div>
                            <p className="local">Hawaii</p>
                            <div className="weather">
                                <div className="elem">
                                    <Wave />
                                    <p className="head-text">9-13</p>
                                    <p className="desc">SURF (FT)</p>
                                </div>
                                <div className="elem">
                                    <Water />
                                    <p className="head-text">+2.3</p>
                                    <p className="desc">TIDE (FT)</p>
                                </div>
                                <div className="elem">
                                    <Wind />
                                    <p className="head-text">4 SE</p>
                                    <p className="desc">WIND (KTS)</p>
                                </div>
                            </div>
                        </div>
                        <div className="point"></div>
                    </div>


                    <div className="suff_currort suff_currort2">
                        <div className="hidden hidden2">
                            <h5 className="name">FORNIA BEACH</h5>
                            <div className="line-suff"></div>
                            <p className="local">California</p>

                            <div className="weather">
                                <div className="elem">
                                    <Wave />
                                    <p className="head-text">7-11</p>
                                    <p className="desc">SURF (FT)</p>
                                </div>
                                <div className="elem">
                                    <Water />
                                    <p className="head-text">+1.8</p>
                                    <p className="desc">TIDE (FT)</p>
                                </div>
                                <div className="elem">
                                    <Wind />
                                    <p className="head-text">3 SE</p>
                                    <p className="desc">WIND (KTS)</p>
                                </div>
                            </div>
                        </div>
                        <div className="point"></div>
                    </div>


                    <div className="suff_currort suff_currort3">
                        <div className="hidden hidden3">
                            <h5 className="name">ROZIO BEACH</h5>
                            <div className="line-suff"></div>
                            <p className="local">Brazil</p>

                            <div className="weather">
                                <div className="elem">
                                    <Wave />
                                    <p className="head-text">6-10</p>
                                    <p className="desc">SURF (FT)</p>
                                </div>
                                <div className="elem">
                                    <Water />
                                    <p className="head-text">+2.1</p>
                                    <p className="desc">TIDE (FT)</p>
                                </div>
                                <div className="elem">
                                    <Wind />
                                    <p className="head-text">5 SE</p>
                                    <p className="desc">WIND (KTS)</p>
                                </div>
                            </div>
                        </div>
                        <div className="point"></div>
                    </div>


                    <div className="suff_currort suff_currort4">
                        <div className="hidden hidden4">
                            <h5 className="name">GURIL BEACH</h5>
                            <div className="line-suff"></div>
                            <p className="local">Portugal</p>

                            <div className="weather">
                                <div className="elem">
                                    <Wave />
                                    <p className="head-text">8-11</p>
                                    <p className="desc">SURF (FT)</p>
                                </div>
                                <div className="elem">
                                    <Water />
                                    <p className="head-text">+1.1</p>
                                    <p className="desc">TIDE (FT)</p>
                                </div>
                                <div className="elem">
                                    <Wind />
                                    <p className="head-text">4 SE</p>
                                    <p className="desc">WIND (KTS)</p>
                                </div>
                            </div>
                        </div>
                        <div className="point"></div>
                    </div>


                    <div className="suff_currort suff_currort5">
                        <div className="hidden hidden5">
                            <h5 className="name">HOTO BEACH</h5>
                            <div className="line-suff"></div>
                            <p className="local">South Africa</p>

                            <div className="weather">
                                <div className="elem">
                                    <Wave />
                                    <p className="head-text">9-15</p>
                                    <p className="desc">SURF (FT)</p>
                                </div>
                                <div className="elem">
                                    <Water />
                                    <p className="head-text">+1.7</p>
                                    <p className="desc">TIDE (FT)</p>
                                </div>
                                <div className="elem">
                                    <Wind />
                                    <p className="head-text">3 SE</p>
                                    <p className="desc">WIND (KTS)</p>
                                </div>
                            </div>
                        </div>
                        <div className="point"></div>
                    </div>

                    <div className="suff_currort suff_currort6">
                        <div className="hidden hidden6">
                            <h5 className="name">SHIRI BEACH</h5>
                            <div className="line-suff"></div>
                            <p className="local">Sri Lanka</p>

                            <div className="weather">
                                <div className="elem">
                                    <Wave />
                                    <p className="head-text">5-9</p>
                                    <p className="desc">SURF (FT)</p>
                                </div>
                                <div className="elem">
                                    <Water />
                                    <p className="head-text">+2.5</p>
                                    <p className="desc">TIDE (FT)</p>
                                </div>
                                <div className="elem">
                                    <Wind />
                                    <p className="head-text">5 SE</p>
                                    <p className="desc">WIND (KTS)</p>
                                </div>
                            </div>
                        </div>
                        <div className="point"></div>
                    </div>


                    <div className="suff_currort suff_currort7">
                        <div className="hidden hidden7">
                            <h5 className="name">AIRLIE BEACH</h5>
                            <div className="line-suff"></div>
                            <p className="local">Australia</p>

                            <div className="weather">
                                <div className="elem">
                                    <Wave />
                                    <p className="head-text">6-11</p>
                                    <p className="desc">SURF (FT)</p>
                                </div>
                                <div className="elem">
                                    <Water />
                                    <p className="head-text">+1.2</p>
                                    <p className="desc">TIDE (FT)</p>
                                </div>
                                <div className="elem">
                                    <Wind />
                                    <p className="head-text">3 SE</p>
                                    <p className="desc">WIND (KTS)</p>
                                </div>
                            </div>
                        </div>
                        <div className="point"></div>
                    </div>

                    <div className="suff_currort suff_currort8">
                        <div className="hidden hidden8">
                            <h5 className="name">TOKAS BEACH</h5>
                            <div className="line-suff"></div>
                            <p className="local">Japan</p>

                            <div className="weather">
                                <div className="elem">
                                    <Wave />
                                    <p className="head-text">10-14</p>
                                    <p className="desc">SURF (FT)</p>
                                </div>
                                <div className="elem">
                                    <Water />
                                    <p className="head-text">+3.0</p>
                                    <p className="desc">TIDE (FT)</p>
                                </div>
                                <div className="elem">
                                    <Wind />
                                    <p className="head-text">6 SE</p>
                                    <p className="desc">WIND (KTS)</p>
                                </div>
                            </div>
                        </div>
                        <div className="point"></div>
                    </div>
            </div>
            <div className="btns">
                <button className='left' ref={prevRef}><Arrow /></button>
                <button className='right' ref={nextRef}><Arrow /></button>
            </div>
            <Swiper
                modules={[Navigation, A11y]}
                navigation={{
                    prevEl: prevRef.current,
                    nextEl: nextRef.current,
                  }}
                  spaceBetween={0}

                breakpoints={{
                    390: {
                        slidesPerView: 1
                    },
                    780: {
                        slidesPerView: 2
                    },
                    1170: {
                        slidesPerView: 3
                    },
                    1920: {
                        slidesPerView: 4
                    },
                }}
                autoHeight={true}
                onInit={() => setInit(true)}
                
                >
                    <SwiperSlide>
                        <div className="slide slide1">
                            <div className="black-background"></div>
                            <div className="content-slide">
                                <h3>Malibu Beach</h3>
                                <p className="vertical">California <span>|</span> USA</p>
                            </div>
                            <a className="hidden">
                                <p>VIEW</p>
                                <div className="blue">
                                    <div className="mini-line"></div>
                                    <p>SURF <Arrow /></p>
                                </div> 
                            </a>
                        </div>
                    </SwiperSlide>

                    <SwiperSlide>
                        <div className="slide slide2">
                        <div className="black-background"></div>
                        <div className="content-slide">
                            <h3>Airlie Beach</h3>
                            <p className="vertical">Queensland <span>|</span>  Australia </p>
                        </div>
                        <a className="hidden">
                            <p>VIEW</p>
                            <div className="blue">
                                <div className="mini-line"></div>
                                <p>SURF <Arrow /></p>
                            </div> 
                        </a>
                    </div>

                    </SwiperSlide>

                    <SwiperSlide>
                        <div className="slide slide3">
                        <div className="black-background"></div>
                        <div className="content-slide">
                            <h3>Cloud Nine</h3>
                            <p className="vertical">Siargao <span>|</span> Philippines</p>
                        </div>
                        
                        <a className="hidden">
                            <p>VIEW</p>
                            <div className="blue">
                                <div className="mini-line"></div>
                                <p>SURF <Arrow /></p>
                            </div> 
                        </a>
                    </div>
                    </SwiperSlide>

                    <SwiperSlide>
                        <div className="slide slide4">
                        <div className="black-background"></div>
                        <div className="content-slide">
                            <h3>Vieux Boucau</h3>
                            <p className="vertical">Hossegor <span>|</span> France</p>
                        </div>
                        <a className="hidden">
                            <p>VIEW</p>
                            <div className="blue">
                                <div className="mini-line"></div>
                                <p>SURF <Arrow /></p>
                            </div> 
                        </a>
                    </div>
                    </SwiperSlide>

                    <SwiperSlide>
                        <div className="slide slide1 slide5">
                            <div className="black-background"></div>
                            <div className="content-slide">
                                <h3>Rozio Beach</h3>
                                <p className="vertical">Salvador <span>|</span> Brazil</p>
                            </div>
                            <a className="hidden">
                                <p>VIEW</p>
                                <div className="blue">
                                    <div className="mini-line"></div>
                                    <p>SURF <Arrow /></p>
                                </div> 
                            </a>
                        </div>
                    </SwiperSlide>

                    <SwiperSlide>
                        <div className="slide slide2 slide6">
                        <div className="black-background"></div>
                        <div className="content-slide">
                            <h3>Hoto Beach</h3>
                            <p className="vertical">Cape Town <span>|</span>  South Africa </p>
                        </div>
                        <a className="hidden">
                            <p>VIEW</p>
                            <div className="blue">
                                <div className="mini-line"></div>
                                <p>SURF <Arrow /></p>
                            </div> 
                        </a>
                    </div>

                    </SwiperSlide>
                    
                </Swiper>
        </article>
    )
}